<template>
<div class="addUser">
    <h3 class="title">Dodaj użytkownika</h3>
    <input class="addUser__input"
    type="text"
    placeholder="Imię"
    v-model.trim="imie">
    <input class="addUser__input"
    type="text"
    placeholder="Nazwisko"
    v-model.trim="nazwisko">
    <select v-model="dzial" class="addUser__input">
        <option value="" disabled selected>Wybierz dział</option>
        <option value="IT">IT</option>
        <option value="Administracja">Administracja</option>
        <option value="Handlowiec">Handlowiec</option>
    </select>
    <input class="addUser__input"
    type="number"
    placeholder="Wynagrodzenie"
    v-model.trim.number="wynagrodzenieKwota">
    <input class="addUser__input"
    type="text"
    placeholder="Waluta"
    v-model.trim="wynagrodzenieWaluta">
    <button class="addUser__button"
    @click.prevent="addUser" :disabled="!isFilled">
      Dodaj użytkownika
    </button>
  </div>
</template>

<script>
export default {
  props: ['pracownicy'],
  data() {
    return {
      imie: '',
      nazwisko: '',
      dzial: '',
      wynagrodzenieKwota: null,
      wynagrodzenieWaluta: '',
    };
  },
  methods: {
    addUser() {
      this.pracownicy.push({
        imie: this.imie,
        nazwisko: this.nazwisko,
        dzial: this.dzial,
        wynagrodzenieKwota: this.wynagrodzenieKwota,
        wynagrodzenieWaluta: this.wynagrodzenieWaluta,
      });
      this.imie = '';
      this.nazwisko = '';
      this.dzial = '';
      this.wynagrodzenieKwota = '';
      this.wynagrodzenieWaluta = '';
    },
  },
  computed: {
    isFilled() {
      return this.imie && this.nazwisko
      && this.dzial
      && this.wynagrodzenieKwota && this.wynagrodzenieWaluta;
    },
  },
};
</script>
<style lang="scss" scoped>
  .addUser{
    &__input{
      padding:5px 8px;
      font-size: 17px;
    }
    &__button{
        background-color: grey; /* Green */
        border: none;
        color: white;
        margin-left:10px;
        padding: 10px 20px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        font-weight: 600;
        cursor: pointer;
        &:disabled{
          background-color:#e4e4e4;
          cursor:not-allowed;
        }
    }
  }
</style>
