<template>
    <div class="rewards">
        <h3 class="title">Suma wynagrodzeń</h3>
        <table class="rewards__table">
            <thead>
                <tr>
                    <th>IT</th>
                    <th>Administracja</th>
                    <th>Handlowiec</th>
                    <th>Całkowita suma</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td class="workers__cell">{{sumWynagrodzenIT}}</td>
                    <td class="workers__cell">{{sumWynagrodzenAdm}}</td>
                    <td class="workers__cell">{{sumWynagrodzenHand}}</td>
                    <td class="workers__cell">
                      {{sumWynagrodzenIT+sumWynagrodzenAdm+sumWynagrodzenHand}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
  props: ['pracownicy'],
  computed: {
    sumWynagrodzenIT() {
      let suma = 0;
      this.pracownicy.forEach((key) => {
        if (key.dzial === 'IT') {
          suma += key.wynagrodzenieKwota;
        }
        return suma;
      });
      return suma;
    },
    sumWynagrodzenAdm() {
      let suma = 0;
      this.pracownicy.forEach((key) => {
        if (key.dzial === 'Administracja') {
          suma += key.wynagrodzenieKwota;
        }
        return suma;
      });
      return suma;
    },
    sumWynagrodzenHand() {
      let suma = 0;
      this.pracownicy.forEach((key) => {
        if (key.dzial === 'Handlowiec') {
          suma += key.wynagrodzenieKwota;
        }
        return suma;
      });
      return suma;
    },
  },
};
</script>
<style lang="scss" scopped>
.rewards{
  width:100%;
  text-align: center;
  &__table{
    width:100%;
  }
}
</style>
