<template>
  <div class="container">
    <div class="content">
      <h1 class="title__h1">Pracownicy</h1>
      <div class="filters">
        <h3 class="title">Filtry</h3>
        <div class="filters__wrapper">
          <div class="filters__name">
            <p class="filters__title">Znajdź osobę</p>
            <input v-model.trim="nameFilter"
            placeholder="Imię lub nazwisko"
            class="filters__input">
          </div>
          <div class="filters__reward">
            <p class="filters__title">Wynagrodzenia </p>
            <input class="filters__reward--input filters__input"
            placeholder="od" v-model.trim.number="wynagroFilterOd">
            -
            <input class="filters__reward--input filters__input"
            placeholder="do" v-model.trim.number="wynagroFilterDo">
          </div>
          <div class="filters__section">
            <p class="filters__title">Działy:</p>
            <ul class="filters__section--list">
              <li class="filters__section--list-item"
              v-for="jedenDzial in dostepneDzialy" :key="jedenDzial.dzial">
                {{jedenDzial.dzial}}
                <input type="checkbox"
                v-model="odznaczoneDzialy"
                v-bind:value="jedenDzial.dzial">
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="workers">
        <table class="workers__table">
          <thead>
            <tr>
              <th class="workers__cell workers__cell--title">Imię</th>
              <th class="workers__cell workers__cell--title">Nazwisko</th>
              <th class="workers__cell workers__cell--title">Dział</th>
              <th class="workers__cell workers__cell--title">Wynagrodzenie</th>
            </tr>
          </thead>
          <tbody>
              <workersList
              :user="user"
              v-for="user in filterOsoba"
              :key="user.nazwisko" />
          </tbody>
        </table>
      </div>
      <sumReward :pracownicy="pracownicy" />
      <addWorker :pracownicy="pracownicy" />
    </div>
  </div>
</template>

<script>
import workersList from './components/workersList.vue';
import sumReward from './components/sumReward.vue';
import addWorker from './components/addWorker.vue';

export default {
  name: 'App',
  components: {
    sumReward, workersList, addWorker,
  },
  data() {
    return {
      pracownicy: [
        {
          imie: 'Jan', nazwisko: 'Kowalski', dzial: 'IT', wynagrodzenieKwota: 3000, wynagrodzenieWaluta: 'PLN',
        },
        {
          imie: 'Anna', nazwisko: 'Bąk', dzial: 'Administracja', wynagrodzenieKwota: 2400.50, wynagrodzenieWaluta: 'PLN',
        },
        {
          imie: 'Paweł', nazwisko: 'Zabłocki', dzial: 'IT', wynagrodzenieKwota: 3300, wynagrodzenieWaluta: 'PLN',
        },
        {
          imie: 'Tomasz', nazwisko: 'Osiecki', dzial: 'Administracja', wynagrodzenieKwota: 2100, wynagrodzenieWaluta: 'PLN',
        },
        {
          imie: 'Iwona', nazwisko: 'Leihs-Gutowska', dzial: 'Handlowiec', wynagrodzenieKwota: 3100, wynagrodzenieWaluta: 'PLN',
        },
      ],
      dzialy: [],
      odznaczoneDzialy: [],
      nameFilter: '',
      wynagroFilterOd: null,
      wynagroFilterDo: null,
      zmiennatest: '',
    };
  },
  computed: {
    filterOsoba() {
      const searchRegex = new RegExp(this.nameFilter, 'i');
      return this.pracownicy.filter((event) => {
        if ((!searchRegex.test(event.imie)) && (!searchRegex.test(event.nazwisko))) {
          return false;
        }
        if (this.wynagroFilterOd > event.wynagrodzenieKwota) {
          return false;
        }
        if ((this.wynagroFilterDo) && (this.wynagroFilterDo < event.wynagrodzenieKwota)) {
          return false;
        }
        if (!this.odznaczoneDzialy.includes(event.dzial)) {
          if (!this.odznaczoneDzialy.length) {
            return this.pracownicy;
          }
          return false;
        }
        return true;
      });
    },
    dostepneDzialy() {
      const dzialy = [];
      this.pracownicy.reduce((newListOfTags, current) => {
        if (!newListOfTags.some((x) => x.dzial === current.dzial)) {
          dzialy.push(current);
        }
        this.dzialy = dzialy;
        return dzialy;
      }, []);
      return dzialy;
    },
  },
};
</script>
